import assign from '../assign'
import { compare as firstVersionNewer, appVersion } from '../version'

const ua = navigator.userAgent || navigator.vendor || window.opera

export default function methodConfig (config = {}) {
  const defaultMethodConfig = {
    isNeedCallback: true,
    defaultParams: '',
    fallback: '',
    deprecated: '',
    supportVersion: '1.4.0',
    iosSupportVersion: '0.0.0',
    supported: true
  }
  const c = assign(defaultMethodConfig, config)
  if (/(iPhone|iPad|iPod|iOS)/i.test(ua)) {
    c.supported = firstVersionNewer(appVersion, c.iosSupportVersion)
  } else {
    c.supported = firstVersionNewer(appVersion, c.supportVersion)
  }
  return c
}
   