import hagoUidReg from '@yy/hago-uid-reg'
import { getCookie } from '@yy/cookie'
import { getParams } from '@yy/url-parameter'

export const URL_PARAMS = getParams()
export const CUR_UID = Number(getCookie('hagouid'))
export const IDC_INFO = hagoUidReg(CUR_UID)
export const CUR_LANG = getLang(URL_PARAMS.lang)
export const ENV = getEnv()
export const H = URL_PARAMS.h || IDC_INFO.urlNo
export default {
  URL_PARAMS, // url参数
  CUR_UID, // 当前账号UID
  IDC_INFO, // hagoUID大区信息
  CUR_LANG, // 当前设备语言
  ENV, // 当前环境 测试or生产
  H // 大区数字码
}

/**
 * 根据url，获取运行时环境信息
 */
function getEnv () {
  let env = 'prod'
  const host = location.host
  if (/^dev/.test(host)) {
    env = 'dev'
  } else if (/^test/.test(host)) {
    env = 'test'
  } else {
    env = 'prod'
  }
  return env
}

/**
 * 获取语言码信息
 * @param {String} hrefLang url上的lang参数
 */
function getLang (hrefLang) {
  let lang = (
    hrefLang ||
    navigator.language ||
    navigator.browserLanguage ||
    'en-US'
  ).split(/-|_/)[0] // 先取url,取不到就取本地的浏览器

  return lang
}
