import appPlatformTester from './tester'
import _injectAppVarToVue from './inject-app-var-to-vue'

export let injectAppVarToVue = _injectAppVarToVue

const ua = navigator.userAgent || navigator.vendor || window.opera

let appPlatform = {}
Object.keys(appPlatformTester).forEach(app => {
  appPlatform[app] = appPlatformTester[app](ua)
})

const sysPlatform = {
  iOS: (/(iPhone|iPad|iPod|iOS)/i.test(ua)),
  android: (/(Android)/i.test(ua))
}

export let appName = ''
export let appNameEN = ''
if (appPlatform.kaixindou) {
  appName = '开心斗'
  appNameEN = 'kxd'
} else if (appPlatform.xunhuan) {
  appName = '寻欢'
  appNameEN = 'xunhuan'
} else if (appPlatform.qingyu) {
  appName = '轻语'
  appNameEN = 'qingyu'
}

export let appCurrency = ''
if (appPlatform.kaixindou) {
  appCurrency = '开心'
} else if (appPlatform.xunhuan) {
  appCurrency = '寻欢'
} else if (appPlatform.qingyu) {
  appCurrency = '轻语'
}

export default {
  app: appPlatform,
  os: sysPlatform,
  appName,
  appNameCN: appName,
  appNameEN,
  appCurrency,
  injectAppVarToVue
}

export const app = appPlatform
export const os = sysPlatform
