import Emitter from '../emitter'

let SEND_SOCKET_DATA
let RECIEVE_SOCKET_DATA
let NOTIFY_SOCKET_DATA

export class Socket extends Emitter {
  constructor (hac, service) {
    super()

    this.service = service
    this.hac = hac
    this.config = hac.config
    this.logger = this.hac.logger

    const { socketConst } = this.config
    SEND_SOCKET_DATA = socketConst.SEND_SOCKET_DATA
    RECIEVE_SOCKET_DATA = socketConst.RECIEVE_SOCKET_DATA
    NOTIFY_SOCKET_DATA = socketConst.NOTIFY_SOCKET_DATA
  }

  send (data) {
    this.hac.callAppMethod({
      module: 'base',
      method: SEND_SOCKET_DATA,
      context: 'sendScoket',
      paramJson: data,
      callbackName: RECIEVE_SOCKET_DATA
    })
  }

  emit (eventName, args) {
    this.send(JSON.stringify({
      service: this.service,
      name: eventName,
      data: typeof args === 'string' ? args : JSON.stringify(args)
    }))
  }

  /**
   *
   * @param {*} funcName
   * @param {*} context
   * @param {Object} paramJson
   * @param {Number} paramJson.code
   * @param {String} paramJson.msg
   * @param {String} paramJson.data
   * {
      "name": "kUriIKXDActivityAsiaGameUserMatchHeartbeat",
      "service": "ikxd_activity_d",
      "data": "{\"code\":1,\"message\":\"successful\",\"data\":{\"round\":1,\"user_infos\":[{\"uid\":101286759,\"sex\":1,\"avatar_url\":\"https://o-hk.ihago.net/ikxd/guest_2.png\",\"nick\":\"hawl4589\",\"ai\":false,\"status\":0}]}}",
     }
   */
  message (funcName, context, paramJson) {
    if (funcName === RECIEVE_SOCKET_DATA) {
      const { code, data, msg } = paramJson
      if (code !== 1) {
        this.logger.warn('[appSocket]', code, data, msg)
      }
      const jData = JSON.parse(data)
      const { name, service, data: BusinessDataStr } = jData

      if (service === this.service) {
        let BusinessData
        try {
          BusinessData = JSON.parse(BusinessDataStr)
        } catch (e) {
          BusinessData = null
        }
        // 客户端报错，name不会回传
        if (code !== 1) {
          console.error(
            `funcName: ${RECIEVE_SOCKET_DATA}`,
            `code: ${code}`,
            `data: ${data}`,
            `msg: ${msg}`,
            `context: ${context}`
          )
        }
        super.emit('message', code, BusinessData, paramJson)
        super.emit(name, code, BusinessData, paramJson)
      }
    } else if (funcName === NOTIFY_SOCKET_DATA) {
      // 主动广播型，无code
      const { name, service, data: BusinessData } = paramJson

      if (service === this.service) {
        const oData = JSON.parse(BusinessData)
        super.emit('message', paramJson)
        super.emit(name, oData)
      }
    }

    return this
  }

  close () {
    super.off()
  }
}

function install (Hac, opts) {
  Hac.prototype._$sockets = Hac.prototype._$sockets || []

  Hac.prototype.$registerSocket = function (service) {
    const instance = new Socket(this, service)
    this._$sockets.push(instance)
    return instance
  }

  Hac.prototype.$removeSocket = function (socketInstance) {
    const index = this._$sockets.indexOf(socketInstance)
    if (index !== -1) {
      this._$sockets.splice(index, 1)
    }
  }
}

export default {
  install
}
