<template>
  <transition :name="transitionName">
    <div class="vue-toast" v-if='show' :class='["vue-toast-" + position, toastClass]'>{{content}}</div>
  </transition>
</template>

<script>
export default {
  name: 'VueToast',
  props: {
    show: {
      type: Boolean,
      required: false,
      default: false
    },
    content: {
      required: false,
      default: '服务器加载失败'
    },
    position: {
      default: 'bottom'
    },
    toastClass: {
      type: Object,
      required: false,
      default: () => ({ 'vue-toast--default': true })
    },
    transitionName: {
      type: String,
      required: false,
      default () {
        return {
          top: 'vue-toast-fade-top',
          middle: '',
          bottom: 'vue-toast-fade-bottom'
        }[this.position] || ''
      }
    }
  }
}
</script>

<style lang='scss'>
.vue-toast {
  left: 20%;
  width: 60%;
  z-index: 10000;
  position: fixed;
  padding: 20px 5px;
  text-align: center;
  font-size: 24px;
  &-bottom {
    bottom: 10%;
  }
  &-top {
    top: 10%;
  }
  &-middle {
    top: 50%;
    margin-top: -22px;
  }
  &--default {
    color: #fff;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.8);
  }
}
.vue-toast-fade-bottom {
  &-enter-active,
  &-leave-active {
    transition: opacity 0.5s, transform 0.3s;
  }
  &-enter,
  &-leave-to {
    opacity: 0;
    transform: translateY(100%);
  }
}
.vue-toast-fade-top {
  &-enter-active,
  &-leave-active {
    transition: opacity 0.5s, transform 0.3s;
  }
  &-enter,
  &-leave-to {
    opacity: 0;
    transform: translateY(-100%);
  }
}
</style>
