export default class StorageManager {
  constructor ({ prefix = '', uid = '', debug }) {
    this.prefix = prefix
    this.debug = debug
    this.uid = uid
  }
  preCheck (ignoreUid, ignorePrefix) {
    if (this.debug) {
      if (!this.uid && !ignoreUid) {
        console.warn('【Storage】key里面没有设置uid!')
      }
      if (!this.prefix && !ignorePrefix) {
        console.warn('【Storage】key里面没有设置prefix前缀标识业务名!')
      }
    }
  }
  getKey (name, ignoreUid, ignorePrefix) {
    let key = ''
    if (!ignorePrefix) {
      key += this.prefix
    }
    if (!ignoreUid) {
      key += this.uid
    }
    return key + name
  }
  getParams (opt) {
    if (typeof opt === 'object') {
      return opt
    } else {
      return {
        key: opt,
        ignoreUid: false,
        ignorePrefix: false
      }
    }
  }
  get (opt) {
    const {
      key,
      ignoreUid = false,
      ignorePrefix = false
    } = this.getParams(opt)
    this.preCheck(ignoreUid, ignorePrefix)
    const finalKey = this.getKey(key, ignoreUid, ignorePrefix)
    let value = window.localStorage.getItem(finalKey)
    try {
      value = JSON.parse(value)
    } catch (e) {}
    if (this.debug) console.log('【Storage】get', finalKey, value)
    return value
  }
  set (opt, content) {
    const {
      key,
      ignoreUid = false,
      ignorePrefix = false
    } = this.getParams(opt)
    content = opt.content || content
    this.preCheck(ignoreUid, ignorePrefix)
    const finalKey = this.getKey(key, ignoreUid, ignorePrefix)
    if (typeof content !== 'string') {
      content = JSON.stringify(content)
    }
    window.localStorage.setItem(finalKey, content)
    if (this.debug) console.log('set', finalKey, content)
    return content
  }
  remove (opt) {
    const {
      key,
      ignoreUid = false,
      ignorePrefix = false
    } = this.getParams(opt)
    this.preCheck(ignoreUid, ignorePrefix)
    const finalKey = this.getKey(key, ignoreUid, ignorePrefix)
    window.localStorage.removeItem(finalKey)
    if (this.debug) console.log('【Storage】remove', finalKey)
  }
  clear () {
    window.localStorage.clear()
    if (this.debug) console.log('【Storage】clear')
  }
  setUid (uid) {
    this.uid = uid
  }
  setPrefix (prefix) {
    this.prefix = prefix
  }
}
