import logger from './logger'

export default function ({
  module,
  method,
  context = '',
  paramJson = '',
  callbackName = ''
}, debug = false) {
  if (debug) {
    logger.debug('[callAppMethod]', { module, method, context, paramJson, callbackName })
  }

  window.nativeApp && window.nativeApp.callAppMethod(module, method, context, paramJson, callbackName)
}
