import { stringify } from './strings'

const CGI_OVERSEA = 'https://hlog.hiido.com/c.gif'
const ACT = 'webzipcachehit'

export const report = (options) => {
  let img = new Image()
  img.onerror = img.onload = function () {
    img = null
  }
  let url = `${CGI_OVERSEA}?act=${ACT}&${stringify(options)}`
  img.src = url
}
