const VER_REGEXP = /^(\d+\.\d+\.\d+).*/
const UA_VER_REGEXP = /yym-hago-and(\d+\.\d+\.\d+)/

let appVersion
if (navigator.appVersion) {
  appVersion = navigator.appVersion.match(UA_VER_REGEXP)
} else {
  appVersion = navigator.userAgent.match(UA_VER_REGEXP)
}
if (appVersion) {
  appVersion = appVersion[1]
} else {
  appVersion = ''
}

export { appVersion }

// "6.2.20" => [6, 2, 20]
const convertVersion2Array = (strVer) => {
  const ver = strVer.replace(VER_REGEXP, '$1')
  const verNums = ver.split('.')
  for (let i = 0, len = verNums.length; i < len; i++) {
    verNums[i] = Number(verNums[i])
  }
  return verNums
}

//  版本比较，第一个版本大于等于第二个，返回true
export const compare = (ver1, ver2) => {
  const ver1Arr = convertVersion2Array(ver1)
  const ver2Arr = convertVersion2Array(ver2)
  const len1 = ver1Arr.length
  const len2 = ver2Arr.length
  const len = len1 > len2 ? len1 : len2

  for (let i = 0; i < len; i++) {
    const p1 = ver1Arr[i]
    const p2 = ver2Arr[i]
    if (p1 > p2) {
      return true
    } else if (p1 < p2) {
      return false
    } else {
      continue
    }
  }

  return true
}
