import VuePageLoading from './vue-page-loading'

let isInit = false
let vm
let output = {}

function init (Vue) {
  if (isInit) return
  isInit = true
  console.log('【VuePageLoading】组件已初始化')
  const VuePageLoadingConstructor = Vue.extend(VuePageLoading)
  const instance = new VuePageLoadingConstructor()
  vm = instance.$mount()
  output.vm = vm
  document.body.appendChild(vm.$el)
}
output = {
  install: init,
  init,
  vm: vm || output.vm
}

export { default as VuePageLoading } from './vue-page-loading'

export default output
