/** =======================================
 * notify通知事件constant
 */
export const appEvents = {
  // socket通道广播消息透传
  // notifyParam: {"service": String(服务), "name": String(服务uri), "data": String(透传数据) }
  NOTIFY_SOCKET_DATA: 'notifySocketData',
  // 配套
  // 更新游戏下载进度
  UPDATE_DOWNLOAD_PROGRESS: 'updateDownloadProgress',
  // 下载状态通知，包括下载进度，和updateDownloadProgress相比这个带了更多信息回来
  // notifyParam:  {"gameId": string, "activityId": string, "status": int, "msg": string}
  ON_DOWNLOAD_STATUS: 'onDownloadStatus',
  // 游戏成功启动
  // notifyParam: {"code": int,"data":"{\"gameId\":string}","msg":""}
  START_LOAD_GAME: 'startLoadGame',
  // 游戏完成
  // notifyParam: {"code": int,"data":"{\"gameId\":string, \"roomId\": string, \"result\": string}","msg":""}
  GAME_FINISHED: 'gameFinished',
  // 前后台切换 { isAppear: Boolean }
  // notifyParam: {"code": int,"data":"{\"isAppear\":boolean}","msg":""}
  ON_FOREGROUND_CHANGED: 'onForegroundChanged',
  // web 调用接口拦截返回行为，APP触发返回时回调给 web
  // notifyParam:  {"type": int}
  ON_INTERCEPT_BACK: 'onInterceptBack',
  // 分享成功的通知，不保证分享成功，只是app调起，配套activityShare使用
  ON_ACTIVITY_SHARE_SUCCESS: 'onActivityShareSuccess'
}
