import assign from '../assign'
const typeMap = {
  TEXT: 0,
  IMAGE: 1,
  LINK: 2,
  VIDEO: 3
}

const shareTypeMap = {
  LINE: 1,
  WHATSAPP: 2,
  INSTAGRAM: 3,
  FACEBOOK: 5,
  MESSENGER: 6,
  VK: 9,
  COPYLINK: 10,
  OTHERS: 0
}

const defaultConfig = {
  activityId: '', // 可选, 活动唯一标识
  shareType: '', // LINE = 1; WHATSAPP = 2;INSTAGRAM = 3;FACEBOOK = 5;MESSENGER = 6;VK = 9;COPYLINK = 10;OTHERS = 0;
  type: 2, // 0--TEXT， 1--IMAGE，2--LINK, 3--VIDEO
  to: 1, // 针对 Facebook， 1--分享给朋友，会调起messege, 2--分享到个人动态
  title: '', // 标题
  imagePath: '', // 本地图片完整路径
  image: '', // 网络图片完整路径
  audioPath: '', // 视频本地完整路径
  webUrl: '', // 跳转 url
  content: '', // 分享的文本内容
  imageBase64: '', // web 传过来的 base64 图片,优先检查image，没有再检查
  isSystemShare: false // 是否系统分享
}

const customKey = ['$autoWrap']

const preConfig = {
  line: {
    shareType: shareTypeMap.LINE,
    type: typeMap.TEXT,
    isSystemShare: true,

    $autoWrap: true
  },
  whatsapp: {
    shareType: shareTypeMap.WHATSAPP,
    type: typeMap.TEXT,
    to: 2,
    isSystemShare: true,

    $autoWrap: true
  },
  instagram: {
    shareType: shareTypeMap.INSTAGRAM,
    type: typeMap.IMAGE,
    isSystemShare: true
  },
  facebook: {
    shareType: shareTypeMap.FACEBOOK,
    type: typeMap.LINK,
    to: 2,
    isSystemShare: false
  },
  messenger: {
    shareType: shareTypeMap.MESSENGER,
    type: typeMap.LINK,
    isSystemShare: false
  },
  vk: {
    shareType: shareTypeMap.VK,
    to: 2,
    type: typeMap.LINK,
    isSystemShare: false
  },
  // 复制content
  copylink: {
    shareType: shareTypeMap.COPYLINK,
    type: typeMap.LINK,
    isSystemShare: false,

    $autoWrap: true
  },
  others: {
    shareType: shareTypeMap.OTHERS,
    type: typeMap.LINK,
    isSystemShare: true
  }
}

function joinContent (config) {
  const { title, content, webUrl } = config
  let con = []
  ;[title, content, webUrl].forEach(val => {
    const _val = val.trim()
    // 不是空串
    if (_val) {
      con.push(val)
    }
  })
  config.content = con.join('\n')
}

function preHandler (config) {
  const { shareType } = config
  switch (shareType) {
    case shareTypeMap.LINE:
      if (config.$autoWrap) { joinContent(config) }
      break
    case shareTypeMap.WHATSAPP:
      if (config.$autoWrap) { joinContent(config) }
      if (config.imagePath || config.image || config.imageBase64) {
        config.type = typeMap.IMAGE
      }
      break
    case shareTypeMap.COPYLINK:
      if (config.$autoWrap) { joinContent(config) }
      break
    default:
      break
  }

  // 清除无关参数
  customKey.forEach(key => {
    delete config[key]
  })
}

function install (Hac, opts) {
  Hac.prototype.$initSaveShare = function () {
    const saveShare = {}
    const self = this
    Object.keys(preConfig).forEach(target => {
      saveShare[target] = function (opts) {
        const config = assign({}, defaultConfig, preConfig[target], opts)

        preHandler(config)
        return self.base.share(config)
      }
    })

    this.saveShare = saveShare
  }
}

export default {
  install
}
