
const logger = {}

;['log', 'debug', 'info', 'warn', 'error'].forEach(method => {
  logger[method] = function (...args) {
    args.unshift(' [hac]') // 打个空格 防止被vconsole正则匹配到...
    if (console[method]) {
      console[method].apply(console, args)
    } else {
      console.log.apply(console, args)
    }
  }
})

export default logger
