import { CUR_LANG, ENV, IDC_INFO } from './constant'

const ua = navigator.userAgent || navigator.vendor || window.opera
const sysPlatform = {
  iOS: (/(iPhone|iPad|iPod|iOS)/i.test(ua)),
  android: (/(Android)/i.test(ua))
}
export default class Request {
  constructor ({ handler, preHandler, postHandler }) {
    const defaultConfig = {
      url: `${getApiPreFix()}/ymicro/api`,
      timeout: 15000,
      withCredentials: true
    }
    if (!handler.defaults) {
      console.warn('未发现Axios')
    }
    this.handler = handler
    this.preHandler = preHandler
    this.postHandler = postHandler
    this.config = defaultConfig
  }

  post (serviceName, methodName, data = {}, retry = 0) {
    const u = this.config.url + `?method=${methodName}`
    const config = {
      method: 'post',
      headers: {
        'X-Lang': CUR_LANG,
        'X-OsType': sysPlatform.android ? 'android' : 'ios',
        'X-Ymicro-Api-Service-Name': serviceName,
        'X-Ymicro-Api-Method-Name': methodName
      },
      data,
      ...this.config,
      url: u
    }
    return new Promise(async (resolve, reject) => {
      let req = () => {
        if (this.preHandler instanceof Function) {
          this.preHandler()
        }
        this.handler(config)
          .then(req => {
            if (this.postHandler instanceof Function) {
              this.postHandler()
            }
            return resolve(req.data)
          })
          .catch(e => {
            if (retry === 0) {
              reject(e)
            } else {
              retry--
              req()
            }
          })
      }
      req()
    })
  }

  // get (url, data, retry = 0) {
  //   const config = {
  //     url,
  //     params: data,
  //     method: 'get',
  //     ...this.config
  //   }
  //   return this.handler(config)
  //     .then(req => req)
  //     .catch(e => {
  //       return Promise.reject(e)
  //     })
  // }
}

function getApiPreFix () {
  if (JSON.stringify(IDC_INFO) === '{}') {
    console.warn('当前UID的IDC_INFO不存在')
  }
  return ENV !== 'prod' ? `//i-test-${IDC_INFO.urlNo || 0}.ihago.net` : `//i-${IDC_INFO.urlNo || 0}.ihago.net`
}
