
const ua = navigator.userAgent
const { hostname } = window.location

const prodHosts = ['www.ihago.net']

const env = prodHosts.indexOf(hostname) > -1 ? 'prod' : 'test'

export const isAppEnv = /hago/i.test(ua)

export const isProd = env === 'prod'

export const getOs = () => {
  return {
    iOS: (/(iPhone|iPad|iPod|iOS)/i.test(ua)),
    android: (/(Android)/i.test(ua))
  }
}

export const getOsName = () => {
  const os = getOs()
  if (os.android) {
    return 'android'
  } else if (os.iOS) {
    return 'ios'
  }
  return ''
}

export const getAppName = () => {
  if (ua.indexOf('yym-hago-and') > -1) {
    return 'hago'
  }
  return ''
}
