import { ajax } from './util/ajax'
import { isProd, isAppEnv } from './util/env'
import { checkUpdate } from './util/client'
import { reportCacheHit } from './cacheHit'

const versionCheckerApi = `https://webapi${isProd ? '' : '-test'}.ihago.net/app/checkVersion`
// const versionCheckerApi = `http://127.0.0.1:3100/app/checkVersion`
const localVersionApi = 'manifest-hash.json'
const INFO = (...args) => {
  window.console && console.info(...args)
}
const doCheckVersion = async ({ debug }) => {
  if (!isAppEnv) {
    INFO(`Check action is ignored! isAppEnv=${isAppEnv}`)
    return {}
  }

  // 获取manifest-hash.json文件，判断是否从本地获取到的
  const manifest = await ajax({ url: localVersionApi })
  if (debug) {
    INFO(`request ${localVersionApi}:`, manifest)
  }
  const { name, hash, zip = false } = manifest

  // 正式环境，上报离线命中率
  if (isProd) {
    reportCacheHit({ name, hit: zip })
  }

  // 检测zip是否有更新
  const result = await ajax({ url: versionCheckerApi, data: { name, hash, zip } })
  if (debug) {
    INFO(`request ${versionCheckerApi}:`, result)
  }
  const { hasNew } = result
  if (hasNew) {
    if (debug) {
      INFO(`[checkUpdate] callAppMethod:base,checkWebZipUpdate`)
    }
    checkUpdate()
  }

  return {
    hasNew
  }
}
export const checkVersion = ({ debug } = {}) => {
  try {
    return doCheckVersion({ debug })
  } catch (err) {
    window.console && console.error('checkVersion error', err)
  }
}
export default checkVersion
