export default {
  wechat: ua => /(MicroMessenger)/i.test(ua),
  weibo: ua => /(Weibo)/i.test(ua),
  qq: ua => /(SQ)/i.test(ua),
  qzone: ua => /(Qzone)/i.test(ua),
  facebook: ua => (ua.indexOf('FBAN') > -1) || (ua.indexOf('FBAV') > -1),
  UC: ua => ua.indexOf('UCBrowser') > -1,
  chrome: ua => /(Chrome)/i.test(ua) && !!window.chrome,
  xunhuan: ua => ua.indexOf('xunhuan') > -1,
  qingyu: ua => ua.indexOf('xunyin') > -1,
  kaixindou: ua => ua.indexOf('langrensha') > -1,
  hago: ua => ua.indexOf('yym-hago-and') > -1
}
