import { osVerStr, osVerInt, appVerStr, appVerInt } from './util/ver'
import { report } from './util/report'
import { getOsName, getAppName } from './util/env'

const endsWith = (str, suffix) => {
  return str.indexOf(suffix, str.length - suffix.length) !== -1
}

const getURI = () => {
  let { hostname, pathname } = window.location
  if (endsWith(pathname, '/')) {
    pathname = `${pathname}index.html`
  }
  return `${hostname}${pathname}`
}
export const reportCacheHit = ({ name, hit, hasNew }) => {
  const time = Math.floor((+new Date()) / 1000)
  const uri = getURI()
  const options = {
    time,
    hit: hit ? 1 : 0,
    uri,
    appversionstring: appVerStr(),
    appversionint: appVerInt(),
    os: getOsName(),
    osversionint: osVerInt(),
    osversionstring: osVerStr(),
    name: name,
    appName: getAppName(),
    hasNew: hasNew ? 1 : 0
  }
  report(options)
}
