/**
 * 订阅发布器
 */
export default class Emitter {
  constructor () {
    this._eventMap = {}
  }

  on (event, fn) {
    if (!this._eventMap[event]) {
      this._eventMap[event] = []
    }
    this._eventMap[event].push(fn)
    return this
  }

  once (event, fn) {
    function onceCb (...args) {
      this.off(event, onceCb)
      fn.apply(this, args)
    }

    onceCb.fn = fn
    this.on(event, onceCb)
    return this
  }

  off (event, fn) {
    const argsLen = arguments.length

    // remove all handlers
    if (argsLen === 0) {
      this._eventMap = {}
      return this
    }

    // event no exist
    if (!this._eventMap[event]) {
      return this
    }

    // remove all specific event handlers
    if (!fn) {
      delete this._eventMap[event]
      return this
    }

    // remove specific handler
    const callbacks = this._eventMap[event]
    for (let i = 0; i < callbacks.length; i++) {
      const cb = callbacks[i]
      if (cb === fn || cb.fn === fn) {
        callbacks.splice(i, 1)
        break
      }
    }

    // remove event which has empty handlers
    if (callbacks.length === 0) {
      delete this._eventMap[event]
    }

    return this
  }

  emit (event, ...args) {
    // console.log(args)
    // args = [].slice.call(arguments, 1)
    // console.log(args)
    const callbacks = this._eventMap[event] || []
    const callbacksCopy = callbacks.slice(0)

    // when event is emitted, the callback should be cured
    // any changes to event listeners when callback executed
    // should occur on the next emit event, not the current one.
    if (callbacksCopy) {
      callbacksCopy.forEach(cb => {
        cb.apply(this, args)
      })
    }

    return this
  }

  listeners (event) {
    return this._eventMap[event] || []
  }

  hasListeners (event) {
    return !!this.listeners(event).length
  }
}
