/**
 * 简单版的deepMerge，内部用，仅仅支持plain object 和 array
 */

function isType (type) {
  return function (obj) {
    return {}.toString.call(obj) === '[object ' + type + ']'
  }
}

const isArray = Array.isArray || isType('Array')
const isObject = isType('Object')

function isMergeable (value) {
  return isObject(value) || isArray(value)
}

function emptyTarget (val) {
  return isArray(val) ? [] : {}
}

function clone (value) {
  if (isMergeable(value)) {
    return deepmerge(emptyTarget(value), value)
  }
  return value
}

function arrayMerge (target, source) {
  return target.concat(source).map((item) => {
    return clone(item)
  })
}

function objectMerge (target, source) {
  const destination = {}
  if (isMergeable(target)) {
    Object.keys(target).forEach((key) => {
      destination[key] = clone(target[key])
    })
  }

  Object.keys(source).forEach((key) => {
    if (!isMergeable(source[key]) || !target[key]) {
      destination[key] = clone(source[key])
    } else {
      destination[key] = deepmerge(target[key], source[key])
    }
  })
  return destination
}

export default function deepmerge (target, source) {
  const targetIsArray = isArray(target)
  const sourceIsArray = isArray(source)
  const sourceAndTargetTypesMatch = targetIsArray === sourceIsArray

  // 类型不同
  if (!sourceAndTargetTypesMatch) {
    return clone(source)
  } else if (sourceIsArray) {
    // 全为数组
    return arrayMerge(target, source)
  } else {
    return objectMerge(target, source)
  }
}
