import querify from './utils/querify.js'
import md5 from 'blueimp-md5'
const CGI = 'https://hlog.hiido.com/c.gif'
const CGI_TEST = 'https://datatest.hiido.com/c.gif'

const ACT = 'kxdwebperformance'

export default function (options, prod = false) {
  const time = Math.floor(Date.now() / 1000)
  let img = new Image()
  img.onerror = img.onload = function () {
    img = null
  }
  options.time = time
  const key = md5(`${ACT}${time}HiidoYYSystem`)
  let url = `${prod ? CGI : CGI_TEST}?act=${ACT}&key=${key}&${querify(options)}`
  img.src = url
}

