import VueToast from './vue-toast'

let isInit = false
let vm
let output = {}
let timer = null
function init (Vue) {
  if (isInit) return
  isInit = true
  console.log('【VueToast】组件已初始化')
  const VueToastConstructor = Vue.extend(VueToast)
  const instance = new VueToastConstructor()
  vm = instance.$mount()
  output.vm = vm
  document.body.appendChild(vm.$el)
}
// if (window.Vue) {
//   init(window.Vue)
// }
output = {
  install: init,
  init,
  vm: vm || output.vm,
  config: {
    delay: 2000
  },
  show (content = '') {
    clearTimeout(timer)
    timer = null
    vm.content = content
    vm.show = true
    timer = setTimeout(_ => {
      vm.show = false
    }, output.config.delay)
  }
}

export { default as VueToast } from './vue-toast'

export default output
