/**
 * hago app和web交互封装
 * web调用app：有callback和无callback
 * app调用web: 回调callback和notify通知
 *
 * callback流程：
 * 发起方是web，使用函数调用的方式
 * 调用 window.nativeApp.callAppMethod('moduleName', 'methodName', 'custom_context', json_string, 'methodNameCallback')
 * app会异步回调 window.callWebMethod('methodName', 'custom_context', json_object)
 *
 * notify流程：
 * 发起方是app，使用事件监听的方式
 * app会调用 window.callWebMethod('notify', 'custom_context', json_object)
 */
import Emitter from './emitter'
import logger from './logger'
import assign from './assign'
import callAppMethod from './call-app-method'
import { appVersion } from './version'
import getConfig from './config'

export default class HagoAppCall extends Emitter {
  static version = '__VERSION__'
  static appVersion = appVersion
  static Emitter = Emitter

  static use (plugin) {
    const installedPlugins = (this._installedPlugins || (this._installedPlugins = []))
    if (installedPlugins.indexOf(plugin) > -1) {
      console.warn('已经安装过', plugin)
      return this
    }
    const args = Array.prototype.slice.call(arguments, 1)
    args.unshift(this)
    if (typeof plugin.install === 'function') {
      plugin.install.apply(plugin, args)
    } else if (typeof plugin === 'function') {
      plugin.apply(null, args)
    }

    installedPlugins.push(plugin)
    return this
  }

  constructor (customConfig) {
    super()

    this.expando = `${(HagoAppCall.version + Math.random()).replace(/\D/g, '')}`
    this.nonce = Date.now()

    this.config = getConfig(customConfig)

    const { debug, appMethods, oldNotify, socketConst, appEvents } = this.config

    this.appEvents = appEvents
    this.debug = debug
    this.appMethods = appMethods
    this.socketConst = socketConst
    this.oldNotify = oldNotify
    this._appMethodsCallback = {}
    this._appMethodsWebCallback = {}
    // this._$sockets = []
    this.$interceptors = {
      pre: null,
      post: null
    }
    this.logger = logger

    this._initFunctionApi(appMethods)
    this._initCallWebviewMethod()
  }

  callAppMethod (req) {
    if (typeof req.paramJson !== 'string') {
      req.paramJson = JSON.stringify(req.paramJson)
    }
    callAppMethod(req, this.debug)
  }

  intercept (inject, provider) {
    this.$interceptors[inject] = this.$interceptors[inject] || []
    this.$interceptors[inject].push(provider)
  }

  _initCallWebviewMethod () {
    let originCWM = null
    if (typeof window.callWebviewMethod === 'function') {
      originCWM = window.callWebviewMethod
    }
    window.callWebviewMethod = async (funcName, context, paramJson) => {
      if (this.debug) {
        logger.debug('[callWebviewMethod]', { funcName, context, paramJson })
      }
      // 兼容旧接口
      if (originCWM) {
        originCWM(funcName, context, paramJson)
      }
      if (this.oldNotify.indexOf(funcName) !== -1) {
        logger.warn(`回调了旧接口${funcName}，hagoAppCall不会处理，直接返回`)
        return
      }

      let resp = { funcName, context, paramJson }
      let [ module, method ] = funcName.split('_')
      // module和method同时存在，走一遍拦截器
      if (module && method) {
        const sInterceptor = this.$interceptors[`${module}.${method}`]
        if (this.$interceptors.post) {
          resp = this.$interceptors.post(resp)
        }

        if (sInterceptor && sInterceptor.length) {
          await this._callStack(sInterceptor, resp)
        }
      }

      // socket逻辑独立处理
      if (
        resp.funcName === this.socketConst.RECIEVE_SOCKET_DATA ||
        resp.funcName === this.socketConst.NOTIFY_SOCKET_DATA
      ) {
        this._$sockets.forEach(socket => {
          socket.message(resp.funcName, resp.context, resp.paramJson)
        })
      } else if (resp.context === 'notify') {
        // 普通notify走事件机制
        this.emit(resp.funcName, resp.paramJson)
      } else if (this._appMethodsCallback[resp.funcName]) {
        // 正常callback走promise回调
        if (this._appMethodsCallback[resp.funcName]) {
          this._appMethodsCallback[resp.funcName](context, paramJson)
        } else {
          logger.warn(`${resp.funcName}回调不存在`)
        }
      } else {
        console.error(`未能处理${resp.funcName} ${resp.context} ${resp.paramJson}`)
      }
    }
  }

  _callStack (handlers, resp) {
    let handlerIndex = 0
    let len = handlers.length
    return new Promise(function (resolve, reject) {
      function next (err) {
        if (err) {
          reject(err)
        } else {
          if (handlerIndex < len) {
            const handler = handlers[handlerIndex]
            handlerIndex++
            handler(resp, next)
          } else {
            resolve()
          }
        }
      }
      next()
    })
  }

  _createContext (methodName) {
    return `hac_${methodName}_${this.expando}_${this.nonce++}`
  }

  _initFunctionApi (config) {
    Object.keys(config).forEach(module => {
      this[module] = {}
      const methods = config[module]

      Object.keys(methods).forEach(method => {
        const methodConfig = methods[method]
        const { isNeedCallback, defaultParams, fallback, supportVersion, supported } = methodConfig
        const fallbackMethodRoad = fallback ? fallback.split('.') : null
        let establishedCbName = ''

        if (isNeedCallback) {
          establishedCbName = `${module}_${method}_callback`
          this._appMethodsCallback[establishedCbName] = (context, paramJson) => {
            const cb = this._appMethodsWebCallback[context]
            delete this._appMethodsWebCallback[context]
            return cb(paramJson)
          }
        }

        this[module][method] = (params) => {
          if (this.debug) {
            logger.debug('传递参数', params)
          }
          // 如果app版本号低于supportVersion，就调用fallback对应的方法
          if (!supported) {
            if (fallbackMethodRoad) {
              logger.warn(`当前版本${appVersion}低于${supportVersion}，回退到${fallback}`)
              return this[fallbackMethodRoad[0]][fallbackMethodRoad[1]](params)
            } else {
              const errMsg = `当前版本${appVersion}低于${supportVersion}，不支持${module}.${method}`
              const err = new Error(errMsg)
              logger.error(errMsg)
              err.code = -404
              return Promise.reject(err)
            }
          }

          // 混合params
          let mixinParams = params
          // 如果是object才做合并处理
          if (typeof params === 'object' && defaultParams) {
            mixinParams = assign({}, defaultParams, params)
          }

          if (isNeedCallback) {
            const p = new Promise((resolve, reject) => {
              const context = this._createContext(method)
              this._appMethodsWebCallback[context] = (rawData) => {
                const { code, msg, data } = rawData
                let cData = data
                if (code === 1) {
                  try {
                    // json字符串
                    cData = JSON.parse(data)
                  } catch (e) {
                    // 解码错误
                    if (/^\{.+\}$/.test(cData)) {
                      logger.warn('注意，对数据进行JSON.parse时出错，检查是否数据返回有误', cData)
                    }
                  }
                  resolve(cData)
                } else {
                  const err = new Error(msg)
                  err.code = code
                  err.msg = msg
                  err.data = cData
                  err.rawData = rawData
                  reject(err)
                }
              }
              this.callAppMethod({
                module,
                method,
                context,
                paramJson: mixinParams,
                callbackName: establishedCbName
              })
            })
            return p
          } else {
            // no need callback, just sync trigger
            this.callAppMethod({
              module,
              method,
              context: '',
              paramJson: mixinParams,
              callbackName: ''
            })
          }
        }
      })
    })
  }
}
