import extra from './extra'
import reportToHiido from './report'
import { getOsName, getAppName } from './utils/platform'
import { osVerStr, appVerStr, intVer } from './utils/ver'
import { getModel } from './utils/model'
import { getFinePathName } from './utils/strings'
import { getMoreInfo } from './moreinfo'

const __VERSION__ = '0.2.2'

const isSupported = !!window.performance

let whiteList = [
  'www.ihago.net'
]
function isInWhiteList (host) {
  return whiteList.indexOf(host) > -1
}

function report (options = {}) {
  if (!isSupported) {
    return console.warn('window.performance不存在，无法计算FMP')
  }
  const t = performance.timing
  const fmp = performance.now ? Math.round(performance.now()) : (Date.now() - t.navigationStart)

  const { hostname } = window.location
  const pathname = getFinePathName()

  const appName = getAppName()
  const appVersionString = appVerStr()
  const appVersionInt = intVer(appVersionString)
  const model = getModel()

  const os = getOsName()
  const osVersionString = osVerStr()
  const osVersionInt = intVer(osVersionString)

  const objExtra = extra()
  let moreInfo = getMoreInfo()

  const dataToReport = {
    ver: __VERSION__,
    uri: `${hostname}${pathname}`,
    fmp,
    extra: JSON.stringify(objExtra),
    appVersionString,
    appVersionInt,
    appName,
    os,
    model,
    osVersionString,
    osVersionInt,
    ...options
  }

  // 扩展moreInfo字段，可被外面复写，moreInfo必须为Object类型
  if (options.moreInfo) {
    moreInfo = { ...moreInfo, ...options.moreInfo }
  }
  dataToReport.moreInfo = JSON.stringify(moreInfo)

  const isProd = isInWhiteList(hostname)
  if (!isProd) {
    console.warn(`当前域名不在白名单${JSON.stringify(whiteList)}内，本次FMP：${fmp}; 上报到测试环境数据如下：`)
    console.warn(dataToReport)
  }

  // 上报到海度，用于采集详细数据
  reportToHiido(dataToReport, isProd)
}

export default function (options = {}) {
  try {
    report(options)
  } catch (e) {
    console.error(e)
  }
}
