const ua = navigator.userAgent || ''

export const os = {
  isIOS: () => (/(iPhone|iPad|iPod|iOS)/i.test(ua)),
  isAndroid: () => (/(Android)/i.test(ua))
}

/**
 * 获取App名字，是HAGO返回hago，否则返回空字符
 * @return {string}
 */
export const getAppName = () => {
  // android和ios都有yym-hago-and
  if (ua.indexOf('yym-hago-and') !== -1) {
    return 'hago'
  }
  return ''
}

/**
 * 获取系统类型，返回android/ios/空字符
 * @return {string}
 */
export const getOsName = () => {
  if (os.isIOS()) {
    return 'ios'
  } else if (os.isAndroid()) {
    return 'android'
  }
  return ''
}
