let Vue

const isArray = v => Object.prototype.toString.call(v) === '[object Array]'
export default class MultLang {
  constructor (opt = {}) {
    this._init = !!opt.message
    const message = opt.messages ? opt.messages[opt.locale] : (opt.message || {})
    this._vm = new Vue({
      data: {
        message
      }
    })
    Vue.prototype.$t = (key, template) => {
      return this.$t(key, template)
    }
  }
  setMessage (message) {
    this._init = true
    this._vm.message = message
  }
  $t (key, template) {
    if (key.indexOf('message.') > -1) {
      key = key.substring(8)
    }
    const vm = this._vm
    const message = vm.message[key]
    if (!message) {
      if (this._init) console.warn(`没有找到key:${key}对应的翻译`)
      return ''
    }
    if (!template) return message
    return this._render(message, template, key)
  }
  _render (message, template, key) {
    if (isArray(template)) {
      return message
        .replace(/{(\d+?)}/g, (all, num) => template[parseInt(num)] || '')
        .replace(/%s(\d)?/g, (all, order = 0) => template[parseInt(order)])
    } else if (template !== null && typeof template === 'object') {
      return message.replace(/{(.+?)}/g, (all, key) => template[key] || '')
    } else {
      throw new Error(`请检查：${key}:${message}，模板填充数据必须是数组或者英文`)
    }
  }
}

MultLang.install = _Vue => {
  if (MultLang.installed) return
  Vue = _Vue
  MultLang.installed = true
}
