// 通过UID计算所在大区
const regionalList = [
  {
    countryZh: '印尼',
    urlNo: 863,
    ossNo: 'id',
    uidNo: 0
  },
  {
    countryZh: '新加坡',
    urlNo: 881,
    ossNo: 'sg',
    uidNo: 6
  },
  {
    countryZh: '印度',
    urlNo: 875,
    ossNo: 'in',
    uidNo: 1
  },
  {
    countryZh: '巴西',
    urlNo: 889,
    ossNo: 'br',
    uidNo: 4
  },
  {
    countryZh: '美国',
    urlNo: 872,
    ossNo: 'sv',
    uidNo: 3
  },
  {
    countryZh: '迪拜',
    urlNo: 894,
    ossNo: 'ua',
    uidNo: 2
  },
  {
    countryZh: '俄罗斯',
    urlNo: 892,
    ossNo: 'de',
    uidNo: 5
  }
]
export default (uid) => {
  const uidNo = Math.floor(parseInt(uid, 10) / 4294967296)
  let IDCInfo = {}
  for (const item of regionalList) {
    if (item.uidNo === uidNo) {
      IDCInfo = item
    }
  }
  return IDCInfo
}
